@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&family=Noto+Serif+KR:wght@700&display=swap');
/* 
  font-family: 'Goldman', cursive;
  font-family: 'Roboto', sans-serif; 
  font-family: 'Nanum Gothic', sans-serif;
  font-family: 'Noto Sans KR', sans-serif;
  font-family: 'Noto Serif KR', serif;
*/

* {
  margin:0;
  padding:0;
  box-sizing: border-box;
}
body { font-size:16px; font-family: 'Roboto', 'Noto Serif KR', sans-serif; }
a { color:inherit; text-decoration:none; }
ul, ol {list-style:none; }
.row { max-width:1200px; margin:0 auto; padding:0 20px; }
.cf:after { content:''; display:block; clear:both }
img { width:100%; vertical-align:middle }
button { cursor:pointer; border:none }
fieldset { display:none }
legend { display:none }
h1, h2, h3, h4, h5, h6 { font-size:100%; }
table { width:100%; border-collapse:collapse; table-layout:fixed }
.blind {
  position:absolute;
  width:1px; height:1px;
  overflow:hidden;
  text-indent:-9999px; 
}

