.homeWrap {
    .article1 {
        
    }
    .article2 {
        .row {
            .local {
                display: flex;
                flex-wrap: wrap;
                padding: 30px 0;
                li {
                    flex: 0 1 33.33%;
                    padding: 20px;
                    a {
                        display: flex;
                        align-items: center;
                        img {
                            width: 50%;
                            margin-right: 10px;
                            border-radius: 10px;
                        }
                    }
                }
            }
            .lifeStyle {
                h2 { font-size: 20px;}
                ul {
                    display: flex;
                    li {
                        flex:1;
                        padding: 20px;
                    }
                }
            }
        }
    }
    .article3 {
        background: #000; color: #fff; padding: 100px 0;
        .row { display:flex; 
            div { 
                flex:1.135;
                img { 
                    padding: 20px 10px 0px; 
                    border-radius: 50px 50px 0px 0px;
                }
                p { 
                    background:#999; margin:0px 10px; 
                    padding:20px; text-align: center;
                    border-radius:0px 0px 30px 30px 
                }
                &.right { 
                    flex:1; display:flex; flex-direction:column; justify-content:space-between;
                    div { width:100%; }
                    .rightUp { display:flex; flex-direction:row; }
                }
            }
        }
    }
}